<template>
  <AppRow permission="CHAT_ADMINISTRATION">
    <template v-slot>
      <v-col cols="12">
        <v-card
          flat
          class="px-4 pb-2"
        >
          <v-card-title class="py-7">
            <v-row>
              <v-col
                cols="12"
                sm="6"
              >
                Запрещённые слова: 0
              </v-col>
              <v-col class="text-right">
                <v-dialog
                  v-model="dialog"
                  persistent
                  max-width="500px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      depressed
                      color="primary"
                      class="text-none"
                      large
                      height="36"
                      v-bind="attrs"
                      v-on="on"
                    >
                      Добавить
                    </v-btn>
                  </template>

                  <v-card>
                    <v-card-title>
                      <span class="text-h5">Добавить слово</span>
                    </v-card-title>

                    <v-card-text>
                      <v-text-field
                        v-model="word"
                        hide-details="auto"
                        label="Слово"
                      />
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer />

                      <v-btn
                        text
                        color="primary"
                        @click="close"
                      >
                        Отмена
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="close"
                      >
                        Добавить
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-col>
            </v-row>
          </v-card-title>
          <v-list v-if="[].length !== 0">
            <template v-for="(item, i) of wordsData.getBlackListWords.words">
              <v-divider :key="`${i}-divider`" />

              <v-list-item
                :key="`${i}-${item}`"
                class="px-6"
              >
                <v-list-item-content>
                  <v-list-item-title>{{ item }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-action>
                  <v-btn
                    :loading="loading"
                    icon
                    color="error"
                  >
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>

              <v-divider
                v-if="wordsData.getBlackListWords.words.length === i + 1"
                :key="`${i}-divider-2`"
              />
            </template>
          </v-list>

          <div
            v-else
            class="text-center grey--text text-body-2 py-4"
          >
            Слова отсутствуют
          </div>

          <div class="text-center pt-3">
            <v-pagination
              v-model="page"
              :total-visible="9"
              :length="Math.ceil(0 / itemsPerPage)"
            />

            <v-select
              v-model="itemsPerPage"
              :items="[5, 10, 15]"
              label="Слов на странице"
              class="pa-4 pt-5"
              @change="firstPage"
            />
          </div>
        </v-card>
      </v-col>

      <v-col>
        <v-dialog
          v-model="dialogMoneyRain"
          persistent
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              block
              depressed
              color="primary"
              class="text-none rounded-lg"
              height="48"
              x-large
              v-bind="attrs"
              v-on="on"
            >
              Денежный дождь
            </v-btn>
          </template>

          <v-card>
            <v-card-title>
              <span class="text-h5">Запустить денежный дождь</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="amount"
                      hide-details="auto"
                      label="Сумма раздачи"
                    />
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="percent"
                      hide-details="auto"
                      label="Процент участникам"
                      suffix="%"
                    />
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                  >
                    <v-menu
                      v-model="menu"
                      :close-on-content-click="false"
                      offset-y
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="date"
                          hide-details="auto"
                          label="Дата"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        />
                      </template>
                      <v-date-picker
                        v-model="date"
                        no-title
                        scrollable
                        @input="menu = false"
                      />
                    </v-menu>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                  >
                    <v-text-field
                      v-model="time"
                      hide-details="auto"
                      label="Время"
                      type="time"
                    />
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer />

              <v-btn
                text
                color="primary"
                @click="closeMoneyRain"
              >
                Отмена
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="closeMoneyRain"
              >
                Включить
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-col>
    </template>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';

export default {
  components: {
    AppRow,
  },
  data() {
    return {
      word: '',
      dialog: false,
      page: 1,
      itemsPerPage: 5,
      dialogMoneyRain: false,
      amount: '',
      percent: '',
      menu: false,
      date: '',
      time: '',
    };
  },
  computed: {
    from() {
      return this.itemsPerPage * (this.page - 1);
    },
    to() {
      return this.from + this.itemsPerPage;
    },
  },
  methods: {
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.word = '';
      });
    },
    firstPage() {
      this.page = 1;
    },
    closeMoneyRain() {
      this.dialogMoneyRain = false;
    },
  },
};
</script>
